import { Query, Mutation } from 'react-apollo';
import Error from './ErrorMessage';
import gql from 'graphql-tag';
import Table from './styles/Table';
import SickButton from './styles/SickButton';
import PropTypes from 'prop-types';

const possiblePreferences = [
  'REQUESTCREATED',
  'REQUESTAPPROVED',
  'REQUESTCONFIRMED',
  'REQUESTREJECTED',
  'REQUESTDELETED',
  'APPOINTMENTTOMORROW',
  'UPDATEDSTRIPEACCOUNT',
  'UPDATEDCARD',
];

const UPDATE_PERMISSIONS_MUTATION = gql`
  mutation updatePreferences($emailPreference: [EmailPreference], $userId: ID!) {
    updatePreferences(emailPreference: $emailPreference, userId: $userId) {
      id
      emailPreference
      name
      lastName
      email
    }
  }
`;

const ALL_USERS_QUERY = gql`
  query {
    users {
      id
      name
      lastName
      email
      price
      shop
      instagramHandle
      image
      permissions
      stripeToken
      artist
    }
  }
`;

const EmailPreferences = props => (
  <Query query={ALL_USERS_QUERY}>
    {({ data, loading, error }) => (
      <div>
        <Error error={error} />
        <div>
          <h2>Manage Permissions</h2>
          <Table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Email</th>
                {possiblePreferences.map(preference => <th key={preference}>{preference}</th>)}
                <th>üëáüèª</th>
              </tr>
            </thead>
            <tbody>{data.users.map(user => <UserPreferences user={user} key={user.id} />)}</tbody>
          </Table>
        </div>
      </div>
    )}
  </Query>
);

class UserPreferences extends React.Component {
  static propTypes = {
    user: PropTypes.shape({
      name: PropTypes.string,
      email: PropTypes.string,
      id: PropTypes.string,
      preferences: PropTypes.array,
    }).isRequired,
  };
  state = {
    preferences: this.props.user.preferences,
  };
  handlePreferenceChange = (e) => {
    const checkbox = e.target;
    // take a copy of the current permissions
    let updatedPreference = [...this.state.preferences];
    // figure out if we need to remove or add this permission
    if (checkbox.checked) {
      // add it in!
      updatedPreference.push(checkbox.value);
    } else {
      updatedPreference = updatedPreferences.filter(preference => permission !== checkbox.value);
    }
    this.setState({ permissions: updatedPermissions });
  };
  render() {
    const user = this.props.user;
    return (
      <Mutation
        mutation={UPDATE_PERMISSIONS_MUTATION}
        variables={{
          permissions: this.state.permissions,
          userId: this.props.user.id,
        }}
      >
        {(updatePermissions, { loading, error }) => (
          <>
            {error && <tr><td colspan="8"><Error error={error} /></td></tr>}
            < tr >
              <td>{user.name} { user.lastName }</td>
              <td>{user.email}</td>
              {possiblePermissions.map(permission => (
                <td key={permission}>
                  <label htmlFor={`${user.id}-permission-${permission}`}>
                    <input
                      id={`${user.id}-permission-${permission}`}
                      type="checkbox"
                      checked={this.state.permissions.includes(permission)}
                      value={permission}
                      onChange={this.handlePermissionChange}
                    />
                  </label>
                </td>
              ))}
              <td>
                <SickButton type="button" disabled={loading} onClick={updatePermissions}>
                  Updat{loading ? 'ing' : 'e'}
                </SickButton>
              </td>
            </tr>
          </>
        )}
      </Mutation>
    );
  }
}

export { ALL_USERS_QUERY }
export { UPDATE_PERMISSIONS_MUTATION };
export default EmailPreferences;
